html {
  font-size: 16px; }
  @media screen and (max-width: 768px) {
    html {
      font-size: 15px; } }
  @media screen and (max-width: 480px) {
    html {
      font-size: 14px; } }

html,
body {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  line-height: 1.42;
  position: relative;
  -webkit-font-snoothing: antialiased;
  -moz-osx-font-snoothing: grayscale; }

.wrapper {
  width: 100%;
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column; }

.container {
  flex: 1; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  font-weight: normal; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none; }

a {
  text-decoration: none; }

.header {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px; }

.social__icon_head {
  fill: #c0d6d5; }
  .social__icon_head:hover {
    fill: #ffffff;
    transition: .5s; }

.hamburger-menu {
  display: block;
  width: 40px;
  height: 2px; }
  .hamburger-menu-bars {
    width: 40px;
    height: 4px;
    background: #ffffff;
    position: relative;
    border-radius: 5px; }
    .hamburger-menu-bars:before {
      content: '';
      position: absolute;
      width: 40px;
      height: 4px;
      background: #ffffff;
      top: 10px;
      border-radius: 5px; }
    .hamburger-menu-bars:after {
      content: '';
      position: absolute;
      width: 40px;
      height: 4px;
      background: #ffffff;
      top: -10px;
      border-radius: 5px; }

.welcome, .blog {
  height: 100vh;
  min-height: 40.625rem;
  background: url("../images/background/mount_bg.jpg") center center no-repeat;
  background-size: cover;
  position: relative; }

.head {
  padding: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.log-in {
  padding: 15px 25px;
  border: 1px solid #f5f6f5;
  color: #f5f6f5;
  border-radius: 5px;
  font-family: 'robotomedium';
  font-size: 16px; }
  .log-in:hover {
    background: #ffffff;
    color: #20c6af;
    transition: .5s; }

.user {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
  height: 395px;
  border-radius: 5px;
  background-color: #fcfcfc;
  opacity: 0.851;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  border-bottom: none; }

.foto {
  overflow: hidden;
  width: 135px;
  height: 135px;
  border-radius: 50%; }
  .foto__user {
    max-width: 100%; }

.name {
  text-align: center; }
  .name__user {
    font: 28px 'robotomedium';
    color: #455a64; }
  .name__description {
    font: 16px 'robotoregular';
    color: #455a64; }

.social {
  display: flex; }
  .social__item {
    margin-right: 20px; }
    .social__item:last-child {
      margin: 0; }
  .social__link {
    display: block; }
  .social__icon {
    fill: #70d1c6; }
    .social__icon:hover {
      fill: #08af9c;
      transition: .5s; }

.navigation {
  width: 100%;
  display: flex; }
  .navigation__item {
    flex: 1 1 auto;
    padding: 12px 25px;
    border-right: 1px solid #ffffff;
    border-bottom: 2px solid #08af9c;
    background: #08af9c; }
    .navigation__item:hover {
      cursor: pointer;
      background: #159689; }
    .navigation__item:last-child {
      border-right: none;
      border-bottom-right-radius: 5px; }
    .navigation__item:first-child {
      border-right: none;
      border-bottom-left-radius: 5px;
      border-right: 1px solid #ffffff; }
  .navigation__link {
    font: 16px 'robotomedium';
    color: #ffffff; }

.dev {
  position: absolute;
  transform: translate(-50%, -50%);
  bottom: 35px;
  left: 50%;
  height: 20px;
  padding: 0 5px;
  border-right: 2px solid #ffffff;
  font: 16px 'robotolight';
  color: #ffffff; }
  .dev:last-child {
    border-right: none; }

.blog {
  background: url("../images/background/blog_bg.jpg") center bottom no-repeat;
  background-size: cover;
  clip-path: polygon(0px 0%, 200% 0px, 50% 100%, 0px 67%, 0px 100px); }

.user-blog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  position: relative; }
  .user-blog:after {
    content: '';
    position: absolute;
    width: 290px;
    height: 200px;
    background: url("../images/icons/blog_header.svg");
    opacity: 0.1;
    top: 50%; }

.user-name {
  margin-top: 50px; }

.title {
  font: 63px 'bernier_regularregular';
  color: #ffffff; }

.user__text {
  font: 16px/1.4rem 'robotoregular';
  color: #ffffff; }

.main-blog {
  display: flex;
  align-items: center;
  justify-content: center; }

.saidbar {
  width: 35%;
  min-height: 63.75rem; }
  .saidbar__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 50px;
    text-align: right; }
  .saidbar__item {
    margin-bottom: 35px; }
    .saidbar__item-active .saidbar__link {
      font: 16px/1.4 'robotobold';
      color: #08af9c;
      color: #00bfa5;
      position: relative; }
      .saidbar__item-active .saidbar__link:after {
        content: '';
        position: absolute;
        width: 2px;
        height: 41px;
        background: #00bfa5;
        right: -15px;
        top: -9px; }
  .saidbar__link {
    display: block;
    font: 16px/1.625rem 'robotoregular';
    color: #455a64; }

.articles {
  width: 65%;
  padding: 3.125rem; }

.article {
  border-bottom: 4px solid #f1f1f2;
  padding-bottom: 65px;
  margin-bottom: 65px; }

.subtitle {
  font: 35px 'bernier_regularregular';
  color: #455a64;
  margin-bottom: 30px; }

.article__data {
  margin-bottom: 30px; }

.data__time {
  font: 16px/1.625rem 'robotoregular';
  color: #455a64;
  color: #00bfa5; }

.article__text {
  font: 16px/1.625rem 'robotoregular';
  color: #455a64;
  margin-bottom: 20px; }

.code {
  width: 100%; }
  .code__img {
    max-width: 100%; }

.footer {
  background: #555b5e; }
  .footer__up {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 3.125rem;
    border-bottom: 4px solid #666c6e; }
  .footer-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%; }
    .footer-nav__link {
      font: 16px/1.62 'robotomedium';
      color: #ffffff; }
      .footer-nav__link:hover {
        color: #08af9c;
        transition: .5s; }
  .footer__down {
    padding: 20px 3.125rem;
    display: flex;
    justify-content: space-between; }
  .footer__description {
    width: 40%; }
  .footer__description, .footer__dev {
    font: 16px/1.63  'robotolight';
    color: #aaadaf; }

.social__icon_footer {
  fill: #aaadaf; }
  .social__icon_footer:hover {
    fill: #ffffff;
    transition: .5s; }
